double {\n  padding: 2.5rem;\n}\n\n.u-padding--double--top {\n  padding-top: 2.5rem;\n}\n\n.u-padding--double--bottom {\n  padding-bottom: 2.5rem;\n}\n\n.u-padding--triple {\n  padding: 3.75rem;\n}\n\n.u-padding--quad {\n  padding: 5rem;\n}\n\n.u-padding--zero {\n  padding: 0;\n}\n\n.u-padding--zero--top {\n  padding-top: 0;\n}\n\n.u-padding--zero--bottom {\n  padding-bottom: 0;\n}\n\n/**\n * Space\n */\n\n.u-space {\n  margin: 1.25rem;\n}\n\n.u-space--top {\n  margin-top: 1.25rem;\n}\n\n.u-space--bottom {\n  margin-bottom: 1.25rem;\n}\n\n.u-space--left {\n  margin-left: 1.25rem;\n}\n\n.u-space--right {\n  margin-right: 1.25rem;\n}\n\n.u-space--quarter {\n  margin: 0.3125rem;\n}\n\n.u-space--quarter--top {\n  margin-top: 0.3125rem;\n}\n\n.u-space--quarter--bottom {\n  margin-bottom: 0.3125rem;\n}\n\n.u-space--quarter--left {\n  margin-left: 0.3125rem;\n}\n\n.u-space--quarter--right {\n  margin-right: 0.3125rem;\n}\n\n.u-space--half {\n  margin: 0.625rem;\n}\n\n.u-space--half--top {\n  margin-top: 0.625rem;\n}\n\n.u-space--half--bottom {\n  margin-bottom: 0.625rem;\n}\n\n.u-space--half--left {\n  margin-left: 0.625rem;\n}\n\n.u-space--half--right {\n  margin-right: 0.625rem;\n}\n\n.u-space--and-half {\n  margin: 1.875rem;\n}\n\n.u-space--and-half--top {\n  margin-top: 1.875rem;\n}\n\n.u-space--and-half--bottom {\n  margin-bottom: 1.875rem;\n}\n\n.u-space--double {\n  margin: 2.5rem;\n}\n\n.u-space--double--top {\n  margin-top: 2.5rem;\n}\n\n.u-space--double--bottom {\n  margin-bottom: 2.5rem;\n}\n\n.u-space--triple {\n  margin: 3.75rem;\n}\n\n.u-space--quad {\n  margin: 5rem;\n}\n\n.u-space--zero {\n  margin: 0;\n}\n\n.u-space--zero--top {\n  margin-top: 0;\n}\n\n.u-space--zero--bottom {\n  margin-bottom: 0;\n}\n\n/**\n * Spacing\n */\n\n.u-spacing > * + * {\n  margin-top: 1.25rem;\n}\n\n@media (max-width: 900px) {\n  .u-spacing--until-large > * + * {\n    margin-top: 1.25rem;\n  }\n}\n\n.u-spacing--quarter > * + * {\n  margin-top: 0.3125rem;\n}\n\n.u-spacing--half > * + * {\n  margin-top: 0.625rem;\n}\n\n.u-spacing--one-and-half > * + * {\n  margin-top: 1.875rem;\n}\n\n.u-spacing--double > * + * {\n  margin-top: 2.5rem;\n}\n\n.u-spacing--triple > * + * {\n  margin-top: 3.75rem;\n}\n\n.u-spacing--quad > * + * {\n  margin-top: 5rem;\n}\n\n.u-spacing--zero > * + * {\n  margin-top: 0;\n}\n\n/* ------------------------------------*    $TRUMPS\n\\*------------------------------------ */\n\n/* ------------------------------------*    $HELPER/TRUMP CLASSES\n\\*------------------------------------ */\n\n.disable-link {\n  pointer-events: none;\n}\n\n.u-overlay,\n.u-overlay--full {\n  position: relative;\n}\n\n.u-overlay::after,\n.u-overlay--full::after {\n  content: '';\n  display: block;\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  top: 0;\n  left: 0;\n  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0.35) 100%) no-repeat border-box;\n  z-index: -1;\n}\n\n.u-overlay--bottom {\n  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%) no-repeat border-box, linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.3) 100%) no-repeat border-box;\n}\n\n/**\n * Clearfix - extends outer container with floated children.\n */\n\n.u-clear-fix {\n  zoom: 1;\n}\n\n.u-clear-fix::after,\n.u-clear-fix::before {\n  content: \" \";\n  display: table;\n}\n\n.u-clear-fix::after {\n  clear: both;\n}\n\n.u-float--right {\n  float: right;\n}\n\n/**\n * Hide elements only present and necessary for js enabled browsers.\n */\n\n.no-js .no-js-hide {\n  display: none;\n}\n\n/**\n * Positioning\n */\n\n.u-position--relative {\n  position: relative;\n}\n\n.u-position--absolute {\n  position: absolute;\n}\n\n/**\n * Alignment\n */\n\n.u-text-align--right {\n  text-align: right;\n}\n\n.u-text-align--center {\n  text-align: center;\n}\n\n.u-text-align--left {\n  text-align: left;\n}\n\n.u-center-block {\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.u-align--center {\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  display: flex;\n  align-items: center;\n}\n\n.u-align--right {\n  margin-left: auto;\n}\n\n/**\n * Background Covered\n */\n\n.u-background--cover {\n  background-size: cover;\n  background-position: center center;\n  background-repeat: no-repeat;\n}\n\n.u-background-image {\n  background-size: 100%;\n  background-repeat: no-repeat;\n}\n\n.u-background--texture {\n  background: #24374d url(\"../images/o-texture--paper.svg\") top -0.125rem center repeat-x;\n  background-size: 110%;\n  overflow: hidden;\n}\n\n/**\n * Flexbox\n */\n\n.u-align-items--center {\n  align-items: center;\n}\n\n.u-align-items--end {\n  align-items: flex-end;\n}\n\n.u-align-items--start {\n  align-items: flex-start;\n}\n\n.u-justify-content--center {\n  justify-content: center;\n}\n\n/**\n * Misc\n */\n\n.u-overflow--hidden {\n  overflow: hidden;\n}\n\n.u-width--100p {\n  width: 100%;\n}\n\n","/* ------------------------------------*\\\n    $MIXINS\n\\*------------------------------------ */\n\n/**\n * Convert px to rem.\n *\n * @param int $size\n *   Size in px unit.\n * @return string\n *   Returns px unit converted to rem.\n */\n@function rem($size) {\n  $remSize: $size / $rembase;\n\n  @return #{$remSize}rem;\n}\n\n/**\n * Center-align a block level element\n */\n@mixin u-center-block {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n/**\n * Standard paragraph\n */\n@mixin p {\n  font-family: $font;\n  font-size: rem(16);\n  line-height: rem(26);\n\n  @include media('>medium') {\n    font-size: rem(18);\n    line-height: rem(28);\n  }\n\n  // @include media('>xlarge') {\n  //   font-size: rem(20);\n  //   line-height: rem(30);\n  // }\n}\n\n/**\n * Maintain aspect ratio\n */\n@mixin aspect-ratio($width, $height) {\n  position: relative;\n\n  &::before {\n    display: block;\n    content: \"\";\n    width: 100%;\n    padding-top: ($height / $width) * 100%;\n  }\n\n  > .ratio-content {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n  }\n}\n","@import \"tools.mixins\";\n\n/* ------------------------------------*\\\n    $VARIABLES\n\\*------------------------------------ */\n\n/**\n * Grid & Baseline Setup\n */\n$fontpx: 16; // Font size (px) baseline applied to <body> and converted to %.\n$defaultpx: 16; // Browser default px used for media queries\n$rembase: 16; // 16px = 1.00rem\n$max-width-px: 1200;\n$max-width: rem($max-width-px) !default;\n\n/**\n * Colors\n */\n$white: #fff;\n$black: #31302e;\n$gray: #b2adaa;\n$error: #f00;\n$valid: #089e00;\n$warning: #fff664;\n$information: #000db5;\n$green: #8d9b86;\n$blue: #24374d;\n$red: #f53d31;\n$tan: #f5f4ed;\n\n/**\n * Style Colors\n */\n$primary-color: $green;\n$secondary-color: $blue;\n$tertiary-color: $red;\n$background-color: $white;\n$link-color: $secondary-color;\n$link-hover: $primary-color;\n$button-color: $tertiary-color;\n$button-hover: darken($tertiary-color, 10%);\n$body-color: $black;\n$border-color: $gray;\n$overlay: rgba(25, 25, 25, 0.6);\n\n/**\n * Typography\n */\n$font: 'Esteban', serif;\n$font-primary: 'Esteban', serif;\n$font-secondary: 'Raleway', sans-serif;\n$sans-serif: \"Helvetica\", sans-serif;\n$serif: Georgia, Times, \"Times New Roman\", serif;\n$monospace: Menlo, Monaco, \"Courier New\", \"Courier\", monospace;\n\n// Questa font weights: 400 700 900\n\n/**\n * Amimation\n */\n$cubic-bezier: cubic-bezier(0.885, -0.065, 0.085, 1.02);\n$ease-bounce: cubic-bezier(0.3, -0.14, 0.68, 1.17);\n\n/**\n * Default Spacing/Padding\n */\n$space: 1.25rem;\n$space-and-half: $space*1.5;\n$space-double: $space*2;\n$space-quad: $space*4;\n$space-half: $space/2;\n$pad: 1.25rem;\n$pad-and-half: $pad*1.5;\n$pad-double: $pad*2;\n$pad-half: $pad/2;\n$pad-quarter: $pad/4;\n$pad-quad: $pad*4;\n$gutters: (mobile: 10, desktop: 10, super: 10);\n$verticalspacing: (mobile: 20, desktop: 30);\n\n/**\n * Icon Sizing\n */\n$icon-xsmall: rem(10);\n$icon-small: rem(20);\n$icon-medium: rem(25);\n$icon-large: rem(60);\n$icon-xlarge: rem(60);\n\n/**\n * Common Breakpoints\n */\n$xsmall: 350px;\n$small: 500px;\n$medium: 700px;\n$large: 900px;\n$xlarge: 1100px;\n$xxlarge: 1300px;\n$xxxlarge: 1500px;\n\n$breakpoints: (\n  'xsmall': $xsmall,\n  'small': $small,\n  'medium': $medium,\n  'large': $large,\n  'xlarge': $xlarge,\n  'xxlarge': $xxlarge,\n  'xxxlarge': $xxxlarge\n);\n\n/**\n * Element Specific Dimensions\n */\n$nav-width: rem(260);\n$article-max: rem(1200);\n$sidebar-width: 320;\n$small-header-height: rem(60);\n$large-header-height: rem(80);\n","/* ------------------------------------*\\\n    $MEDIA QUERY TESTS\n\\*------------------------------------ */\n@if $tests == true {\n  body {\n    &::before {\n      display: block;\n      position: fixed;\n      z-index: 100000;\n      background: black;\n      bottom: 0;\n      right: 0;\n      padding: 0.5em 1em;\n      content: 'No Media Query';\n      color: transparentize(#fff, 0.25);\n      border-top-left-radius: 10px;\n      font-size: (12/16)+em;\n\n      @media print {\n        display: none;\n      }\n    }\n\n    &::after {\n      display: block;\n      position: fixed;\n      height: 5px;\n      bottom: 0;\n      left: 0;\n      right: 0;\n      z-index: (100000);\n      content: '';\n      background: black;\n\n      @media print {\n        display: none;\n      }\n    }\n\n    @include media('>xsmall') {\n      &::before {\n        content: 'xsmall: 350px';\n      }\n\n      &::after,\n      &::before {\n        background: dodgerblue;\n      }\n    }\n\n    @include media('>small') {\n      &::before {\n        content: 'small: 500px';\n      }\n\n      &::after,\n      &::before {\n        background: darkseagreen;\n      }\n    }\n\n    @include media('>medium') {\n      &::before {\n        content: 'medium: 700px';\n      }\n\n      &::after,\n      &::before {\n        background: lightcoral;\n      }\n    }\n\n    @include media('>large') {\n      &::before {\n        content: 'large: 900px';\n      }\n\n      &::after,\n      &::before {\n        background: mediumvioletred;\n      }\n    }\n\n    @include media('>xlarge') {\n      &::before {\n        content: 'xlarge: 1100px';\n      }\n\n      &::after,\n      &::before {\n        background: hotpink;\n      }\n    }\n\n    @include media('>xxlarge') {\n      &::before {\n        content: 'xxlarge: 1300px';\n      }\n\n      &::after,\n      &::before {\n        background: orangered;\n      }\n    }\n\n    @include media('>xxxlarge') {\n      &::before {\n        content: 'xxxlarge: 1400px';\n      }\n\n      &::after,\n      &::before {\n        background: dodgerblue;\n      }\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $RESET\n\\*------------------------------------ */\n\n/* Border-Box http:/paulirish.com/2012/box-sizing-border-box-ftw/ */\n* {\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n}\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  margin: 0;\n  padding: 0;\n}\n\narticle,\nfigure,\nfooter,\nheader,\nhgroup,\nnav,\nsection {\n  display: block;\n}\n","/* ------------------------------------*\\\n    $TEXT TYPES\n\\*------------------------------------ */\n\n/**\n * Text Primary\n */\n\n@mixin u-font--primary--xl() {\n  font-size: rem(40);\n  line-height: rem(55);\n  font-family: $font-primary;\n  font-weight: 400;\n\n  @include media ('>large') {\n    font-size: rem(60);\n    line-height: rem(75);\n  }\n}\n\n.u-font--primary--xl,\nh1 {\n  @include u-font--primary--xl;\n}\n\n@mixin u-font--primary--l() {\n  font-size: rem(26);\n  line-height: rem(36);\n  font-family: $font-primary;\n  font-weight: 400;\n\n  @include media ('>large') {\n    font-size: rem(36);\n    line-height: rem(46);\n  }\n}\n\n.u-font--primary--l,\nh2 {\n  @include u-font--primary--l;\n}\n\n@mixin u-f