1.5;\n}\n\nbutton,\ninput,\nselect,\ntextarea {\n  -webkit-appearance: none;\n  -webkit-border-radius: 0;\n}\n\ninput[type=email],\ninput[type=number],\ninput[type=search],\ninput[type=tel],\ninput[type=text],\ninput[type=url],\ntextarea,\nselect {\n  border: 1px solid $border-color;\n  background-color: $white;\n  width: 100%;\n  outline: 0;\n  display: block;\n  transition: all 0.5s $cubic-bezier;\n  padding: $pad-half;\n}\n\ninput[type=\"search\"] {\n  -webkit-appearance: none;\n  border-radius: 0;\n}\n\ninput[type=\"search\"]::-webkit-search-cancel-button,\ninput[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * Form Field Container\n */\n.field-container {\n  margin-bottom: $space;\n}\n\n/**\n * Validation\n */\n.has-error {\n  border-color: $error;\n}\n\n.is-valid {\n  border-color: $valid;\n}\n","/* ------------------------------------*\\\n    $HEADINGS\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $LINKS\n\\*------------------------------------ */\na {\n  text-decoration: none;\n  color: $link-color;\n  transition: all 0.6s ease-out;\n  cursor: pointer;\n\n  &:hover {\n    text-decoration: none;\n    color: $link-hover;\n  }\n\n  p {\n    color: $body-color;\n  }\n}\n\n.u-link--cta {\n  @include u-font--secondary--s;\n\n  color: $link-color;\n  display: table;\n\n  .u-icon {\n    transition: all 0.25s ease;\n    left: $space;\n    position: relative;\n  }\n\n  &:hover {\n    .u-icon {\n      left: rem(23);\n    }\n  }\n}\n\n.u-link--white {\n  color: $white;\n\n  &:hover {\n    color: $gray;\n\n    .u-icon {\n      path {\n        fill: $gray;\n      }\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $LISTS\n\\*------------------------------------ */\nol,\nul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n/**\n * Definition Lists\n */\ndl {\n  overflow: hidden;\n  margin: 0 0 $space;\n}\n\ndt {\n  font-weight: bold;\n}\n\ndd {\n  margin-left: 0;\n}\n","/* ------------------------------------*\\\n    $SITE MAIN\n\\*------------------------------------ */\n\nbody {\n  background: $background-color;\n  font: 400 100%/1.3 $font;\n  -webkit-text-size-adjust: 100%;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: $body-color;\n  overflow-x: hidden;\n}\n\n.preload * {\n  -webkit-transition: none !important;\n  -moz-transition: none !important;\n  -ms-transition: none !important;\n  -o-transition: none !important;\n  transition: none !important;\n}\n","/* ------------------------------------*\\\n    $MEDIA ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Flexible Media\n */\niframe,\nimg,\nobject,\nsvg,\nvideo {\n  max-width: 100%;\n  border: none;\n}\n\nimg[src$=\".svg\"] {\n  width: 100%;\n}\n\npicture {\n  display: block;\n  line-height: 0;\n}\n\nfigure {\n  max-width: 100%;\n\n  img {\n    margin-bottom: 0;\n  }\n}\n\n.fc-style,\nfigcaption {\n  font-size: rem(14);\n  padding-top: rem(3);\n  margin-bottom: rem(5);\n}\n\n.clip-svg {\n  height: 0;\n}\n\n/* ------------------------------------*\\\n    $PRINT STYLES\n\\*------------------------------------ */\n@media print {\n  *,\n  *::after,\n  *::before,\n  *::first-letter,\n  *::first-line {\n    background: transparent !important;\n    color: $black !important;\n    box-shadow: none !important;\n    text-shadow: none !important;\n  }\n\n  a,\n  a:visited {\n    text-decoration: underline;\n  }\n\n  a[href]::after {\n    content: \" (\" attr(href) \")\";\n  }\n\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\";\n  }\n\n  /*\n   * Don't show links that are fragment identifiers,\n   * or use the `javascript:` pseudo protocol\n   */\n  a[href^=\"#\"]::after,\n  a[href^=\"javascript:\"]::after {\n    content: \"\";\n  }\n\n  blockquote,\n  pre {\n    border: 1px solid $border-color;\n    page-break-inside: avoid;\n  }\n\n  /*\n   * Printing Tables:\n   * http://css-discuss.incutio.com/wiki/Printing_Tables\n   */\n  thead {\n    display: table-header-group;\n  }\n\n  img,\n  tr {\n    page-break-inside: avoid;\n  }\n\n  img {\n    max-width: 100% !important;\n  }\n\n  h2,\n  h3,\n  p {\n    orphans: 3;\n    widows: 3;\n  }\n\n  h2,\n  h3 {\n    page-break-after: avoid;\n  }\n\n  #footer,\n  #header,\n  .ad,\n  .no-print {\n    display: none;\n  }\n}\n","/* ------------------------------------*\\\n    $TABLES\n\\*------------------------------------ */\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  border: 1px solid $border-color;\n  width: 100%;\n}\n\nth {\n  text-align: left;\n  border: 1px solid $border-color;\n  padding: 0.2em;\n}\n\ntd {\n  border: 1px solid $border-color;\n  padding: 0.2em;\n}\n","/* ------------------------------------*\\\n    $TEXT ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Abstracted paragraphs\n */\np,\nul,\nol,\ndt,\ndd,\npre {\n  @include p;\n}\n\n/**\n * Bold\n */\nb,\nstrong {\n  font-weight: 700;\n}\n\n/**\n * Horizontal Rule\n */\nhr {\n  height: 1px;\n  border: none;\n  background-color: $border-color;\n\n  @include u-center-block;\n}\n\n/**\n * Abbreviation\n */\nabbr {\n  border-bottom: 1px dotted $border-color;\n  cursor: help;\n}\n","/* ------------------------------------*\\\n    $GRIDS\n\\*------------------------------------ */\n\n/**\n * Simple grid - keep adding more elements to the row until the max is hit\n * (based on the flex-basis for each item), then start new row.\n */\n.l-grid {\n  display: flex;\n  display: inline-flex;\n  flex-flow: row wrap;\n}\n\n/**\n * Fixed Gutters\n */\n@mixin column-gutters() {\n  padding-left: $pad/1.5;\n  padding-right: $pad/1.5;\n\n  @include media ('>xlarge') {\n    &.u-left-gutter--l {\n      padding-left: rem(30);\n    }\n\n    &.u-right-gutter--l {\n      padding-right: rem(30);\n    }\n\n    &.u-left-gutter--xl {\n      padding-left: rem(60);\n    }\n\n    &.u-right-gutter--xl {\n      padding-right: rem(60);\n    }\n  }\n}\n\n[class*=\"grid--\"] {\n  &.u-no-gutters {\n    margin-left: 0;\n    margin-right: 0;\n\n    > .l-grid-item {\n      padding-left: 0;\n      padding-right: 0;\n    }\n  }\n\n  > .l-grid-item {\n    box-sizing: border-box;\n\n    @include column-gutters();\n  }\n}\n\n@mixin layout-in-column {\n  margin-left: -1 * $space/1.5;\n  margin-right: -1 * $space/1.5;\n\n  @include media ('>xlarge') {\n    margin-left: -1 * $space/1.5;\n    margin-right: -1 * $space/1.5;\n  }\n}\n\n[class*=\"l-grid--\"] {\n  @include layout-in-column;\n}\n\n.l-grid-item {\n  width: 100%;\n  box-sizing: border-box;\n}\n\n/**\n* 1 to 2 column grid at 50% each.\n*/\n.l-grid--50-50 {\n  @include media ('>medium') {\n    width: 100%;\n\n    > * {\n      width: 50%;\n    }\n  }\n}\n\n/**\n * 3 column grid\n */\n.l-grid--3-col {\n  margin: 0;\n\n  @include media ('>medium') {\n    width: 100%;\n\n    > * {\n      width: 33.3333%;\n    }\n  }\n}\n\n/**\n * 4 column grid\n */\n.l-grid--4-col {\n  > * {\n    margin-bottom: $space*1.5;\n    display: flex;\n    align-items: stretch;\n  }\n\n  @include media('>small') {\n    > * {\n      width: 50%;\n    }\n  }\n\n  @include media('>medium') {\n    width: 100%;\n  }\n\n  @include media('>large') {\n    > * {\n      width: 25%;\n    }\n  }\n}\n\n.l-grid--photos {\n  column-count: 2;\n  -moz-column-gap: $space;\n  -webkit-column-gap: $space;\n  column-gap: $space;\n  display: block;\n  padding: 0;\n  margin: 0;\n\n  > .l-grid-item {\n    display: block;\n    margin: 0 auto;\n    padding: 0;\n    margin-bottom: $space;\n    width: 100%;\n  }\n\n  @include media('>small') {\n    column-count: 3;\n  }\n\n  @include media('>medium') {\n    column-count: 4;\n  }\n\n  @include media('>xxlarge') {\n    column-count: 5;\n  }\n}\n","/* ------------------------------------*\\\n    $WRAPPERS & CONTAINERS\n\\*------------------------------------ */\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n.l-container {\n  margin: 0 auto;\n  position: relative;\n  padding-left: $pad;\n  padding-right: $pad;\n\n  @include media('>xlarge') {\n    padding-left: $pad*2;\n    padding-right: $pad*2;\n  }\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n.l-wrap {\n  max-width: $max-width;\n  margin: 0 auto;\n}\n\n/**\n * Wrapping element to keep content contained and centered at narrower widths.\n */\n.l-narrow {\n  max-width: rem(800);\n  margin: 0 auto;\n}\n\n.l-narrow--xs {\n  max-width: rem(500);\n}\n\n.l-narrow--s {\n  max-width: rem(600);\n}\n\n.l-narrow--m {\n  max-width: rem(700);\n}\n\n.l-narrow--l {\n  max-width: $article-max;\n}\n\n.l-narrow--xl {\n  max-width: rem(1300);\n}\n","/* ------------------------------------*\\\n    $BLOCKS\n\\*------------------------------------ */\n\n.c-block__default {\n  .l-grid {\n    margin: 0;\n    display: flex;\n  }\n\n  .c-block__media {\n    min-height: rem(250);\n    background-color: $tan;\n    background-size: cover;\n\n    @include media('>large') {\n      min-height: rem(300);\n    }\n  }\n\n  .c-block__content {\n    text-decoration: none;\n    display: flex;\n    justify-content: space-between;\n    flex-direction: column;\n  }\n}\n\n.c-block__link {\n  &:hover {\n    color: inherit;\n  }\n}\n\n.c-block-news {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  min-height: rem(400);\n  width: 100%;\n\n  .c-block__button {\n    display: flex;\n    justify-content: space-between;\n    border-top: 1px solid $border-color;\n  }\n\n  .c-block__link {\n    position: relative;\n  }\n\n  .c-block__title,\n  .c-block__date,\n  .c-block__excerpt {\n    font-weight: normal;\n  }\n\n  .c-block__date,\n  .c-block__excerpt {\n    color: $black;\n  }\n\n  .c-block__title {\n    color: $secondary-color;\n  }\n\n  .c-block__link,\n  .c-block__content {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    height: 100%;\n    align-items: stretch;\n    transition: all 0.25s ease-in-out;\n    top: auto;\n  }\n\n  &.has-hover {\n    .c-block__excerpt {\n      display: none;\n    }\n  }\n}\n\n.touch .c-block-news {\n  .c-block__excerpt {\n    display: block;\n  }\n}\n\n.no-touch .c-block-news:hover {\n  .c-block__content {\n    position: absolute;\n    top: 0;\n    background: $tan;\n    width: 100%;\n  }\n\n  .c-block__excerpt {\n    display: block;\n  }\n\n  .c-block__button {\n    background-color: $primary-color;\n    color: white;\n\n    .u-icon path {\n      fill: $white;\n    }\n  }\n}\n\n.c-block-events {\n  .c-block__link {\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    border: 1px solid $black;\n    margin-bottom: $space;\n    position: relative;\n\n    @include media('>small') {\n      flex-direction: row;\n      height: rem(200);\n      margin-top: rem(-1);\n      margin-bottom: 0;\n    }\n\n    &.disable {\n      pointer-events: none;\n\n      .u-icon {\n        display: none;\n      }\n    }\n  }\n\n  .c-block__day {\n    position: relative;\n    display: block;\n    width: 100%;\n\n    @include media('>small') {\n      width: rem(40);\n      height: auto;\n    }\n\n    &::after {\n      @include u-font--secondary--s;\n\n      content: attr(data-content);\n      text-align: center;\n      display: block;\n      color: $gray;\n      line-height: rem(40);\n      width: 100%;\n      height: rem(40);\n      background-color: $black;\n\n      @include media('>small') {\n        background-color: transparent;\n        transform: rotate(-90deg);\n        width: rem(200);\n        height: rem(200);\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n      }\n    }\n  }\n\n  .c-block__date {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    padding: $pad;\n    position: absolute;\n    top: rem(40);\n    background-color: $primary-color;\n    color: $white;\n    z-index: 1;\n\n    @include media('>small') {\n      position: relative;\n      top: auto;\n      border-right: 1px solid $black;\n      background-color: $white;\n      color: $black;\n      min-width: rem(80);\n    }\n  }\n\n  .c-block__date + .c-block__content {\n    @include media('<=small') {\n      padding-left: rem(100);\n    }\n  }\n\n  .c-block__media {\n    position: relative;\n    min-height: rem(250);\n\n    @include media('>small') {\n      width: rem(500);\n      height: 100%;\n      min-height: auto;\n      display: block;\n    }\n  }\n\n  .c-block__content {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    flex-direction: column;\n    width: 100%;\n\n    @include media('>small') {\n      flex: auto;\n      flex-direction: row;\n    }\n  }\n\n  .c-block__header {\n    width: 100%;\n    justify-content: flex-start;\n    text-align: left;\n    display: flex;\n    flex-direction: column;\n    flex: auto;\n\n    @include media('>small') {\n      padding-right: $pad*2;\n    }\n  }\n\n  .c-block__excerpt {\n    @include media('>small') {\n      overflow: hidden;\n      text-overflow: ellipsis;\n      display: -webkit-box;\n      -webkit-line-clamp: 2;\n      -webkit-box-orient: vertical;\n    }\n  }\n\n  .u-icon {\n    display: none;\n    height: rem(11);\n    position: relative;\n    right: $space/2;\n    transition: right 0.25s ease-in-out;\n\n    @include media('>small') {\n      display: inline-block;\n    }\n  }\n\n  &:hover {\n    .u-icon {\n      right: 0;\n    }\n  }\n}\n\n.c-block-featured-page {\n  position: relative;\n  padding: 0 !important;\n  margin: 0;\n  overflow: hidden;\n\n  .c-block__content {\n    display: flex;\n    justify-content: space-between;\n    flex-direction: column;\n    min-height: rem(300);\n    z-index: 1;\n\n    @include media('>medium') {\n      min-height: rem(400);\n    }\n\n    @include media('>large') {\n      min-height: rem(550);\n    }\n  }\n\n  .c-block__media {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    margin: auto;\n    width: 110%;\n    height: 110%;\n    z-index: -1;\n    transform: scale(1);\n    transition: transform 0.25s ease;\n  }\n\n  &:hover {\n    .c-block__media {\n      -webkit-filter: blur(2px);\n      filter: blur(2px);\n      transform: scale(1.1);\n    }\n\n    .o-button {\n      background-color: $button-color;\n      border-color: $button-color;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $BUTTONS\n\\*------------------------------------ */\n\n.o-button,\nbutton,\ninput[type=\"submit\"],\na.fasc-button {\n  cursor: pointer;\n  box-shadow: none;\n  border: 0;\n  transition: all 0.25s ease-in-out;\n  position: relative;\n  padding: $pad/1.5 $pad*3 $pad/1.5 $pad;\n  margin: $space 0 0 0;\n  display: table;\n  vertical-align: middle;\n  text-align: center;\n  width: auto;\n  background: $button-color;\n  color: $button-hover;\n  border-radius: rem(50);\n\n  @include u-font--secondary--s;\n\n  &:focus {\n    outline: 0;\n  }\n\n  &:hover {\n    background-color: $button-hover;\n    color: $white;\n\n    &::after {\n      background: url('../assets/images/o-arrow--white--short.svg') center center no-repeat;\n      background-size: rem(30);\n      right: rem(15);\n    }\n  }\n\n  &::after {\n    content: '';\n    display: block;\n    margin-left: $space-half;\n    background: url('../assets/images/o-arrow--white--short.svg') center center no-repeat;\n    background-size: rem(30);\n    width: rem(30);\n    height: rem(30);\n    position: absolute;\n    right: $space;\n    top: 50%;\n    transform: translateY(-50%);\n    transition: all 0.25s ease-in-out;\n  }\n}\n\n.u-button--red {\n  color: $white;\n  background-color: $tertiary-color;\n\n  &:hover {\n    background-color: darken($tertiary-color, 10%);\n    color: $white;\n  }\n}\n\n.u-button--green {\n  color: $white;\n  background-color: $primary-color;\n\n  &:hover {\n    background-color: darken($primary-color, 10%);\n    color: $white;\n  }\n}\n\n.u-button--outline {\n  color: $white;\n  background-color: transparent;\n  border: 1px solid $white;\n\n  &:hover {\n    background-color: $button-color;\n    color: $white;\n    border: 1px solid $button-color;\n  }\n}\n\na.fasc-button {\n  background: $button-color !important;\n  color: $button-hover !important;\n\n  &:hover {\n    background-color: $button-hover !important;\n    color: $white !important;\n    border-color: $button-hover;\n  }\n}\n\n.u-button--search {\n  padding: rem(5);\n  background-color: transparent;\n\n  &:hover {\n    background-color: transparent;\n  }\n\n  &::after {\n    display: none;\n  }\n}\n\n.ajax-load-more-wrap {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n}\n\n.alm-load-more-btn.done {\n  pointer-events: none;\n  opacity: 0.4;\n  background-color: $gray;\n  border-color: $gray;\n}\n\n.alm-btn-wrap {\n  width: 100%;\n}\n","/* ------------------------------------*\\\n    $MESSAGING\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $ICONS\n\\*------------------------------------ */\n.u-icon {\n  display: inline-block;\n\n  path {\n    transition: all 0.25s ease-in-out;\n  }\n}\n\n.u-icon--xs {\n  width: $icon-xsmall;\n  height: $icon-xsmall;\n}\n\n.u-icon--s {\n  width: $icon-small;\n  height: $icon-small;\n}\n\n.u-icon--m {\n  width: $icon-medium;\n  height: $icon-medium;\n}\n\n.u-icon--l {\n  width: $icon-large;\n  height: $icon-large;\n}\n\n.u-icon--xl {\n  width: $icon-xlarge;\n  height: $icon-xlarge;\n}\n\n.u-icon--arrow-prev {\n  background: url('../assets/images/o-arrow--carousel--prev.svg') center center no-repeat;\n  left: 0;\n  background-size: rem(15) auto;\n}\n\n.u-icon--arrow-next {\n  background: url('../assets/images/o-arrow--carousel--next.svg') center center no-repeat;\n  right: 0;\n  background-size: rem(15) auto;\n}\n\n.u-icon--arrow--small {\n  background: url('../assets/images/o-arrow--small.svg') center center no-repeat;\n  left: 0;\n  background-size: rem(10) auto;\n}\n","/* ------------------------------------*\\\n    $LIST TYPES\n\\*------------------------------------ */\n\n.u-list__title {\n  margin-bottom: $space;\n}\n\n.u-list__details {\n  border-left: 1px solid $gray;\n  padding-left: $pad;\n}\n","/* ------------------------------------*\\\n    $NAVIGATION\n\\*------------------------------------ */\n\n.c-nav__primary {\n  position: absolute;\n  top: $small-header-height;\n  left: 0;\n  width: 100%;\n  background-color: $tan;\n  box-shadow: 0 2px 0 rgba($gray, 0.4);\n  transition: none;\n\n  @include media('>xlarge') {\n    position: relative;\n    top: 0;\n    background-color: transparent;\n    box-shadow: none;\n    width: auto;\n  }\n\n  &.is-active {\n    .c-primary-nav__list {\n      display: block;\n    }\n\n    .c-nav__toggle {\n      .c-nav__toggle-span--1 {\n        opacity: 0;\n      }\n\n      .c-nav__toggle-span--2 {\n        transform: rotate(45deg);\n        top: rem(-4);\n        right: rem(-2);\n      }\n\n      .c-nav__toggle-span--3 {\n        transform: rotate(-45deg);\n        top: rem(-10);\n        right: rem(-2);\n      }\n\n      .c-nav__toggle-span--4::after {\n        content: \"Close\";\n      }\n    }\n  }\n}\n\n.c-nav__toggle {\n  position: absolute;\n  padding: $pad;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: $small-header-height;\n  width: $small-header-height;\n  top: -$small-header-height;\n  right: 0;\n\n  @include media('>xlarge') {\n    display: none;\n  }\n\n  .c-nav__toggle-span {\n    display: block;\n    background-color: $white;\n    width: rem(30);\n    height: rem(1);\n    margin-bottom: rem(5);\n    transition: transform 0.25s ease;\n    position: relative;\n    border: 0;\n    outline: 0;\n  }\n\n  .c-nav__toggle-span--4 {\n    margin: 0;\n    background-color: transparent;\n    height: auto;\n    color: $white;\n    display: block;\n\n    &::after {\n      position: absolute;\n      top: 0;\n      left: 0;\n      right: 0;\n      margin: 0 auto;\n      text-align: center;\n      content: \"Menu\";\n      padding-top: rem(3);\n      font-family: $font-secondary;\n      text-transform: uppercase;\n      font-weight: 700;\n      line-height: rem(3);\n      letter-spacing: rem(1.25);\n      font-size: rem(3);\n    }\n  }\n}\n\n.c-primary-nav__list {\n  height: auto;\n  width: 100%;\n  display: none;\n\n  @include media('>xlarge') {\n    display: flex;\n    flex-direction: row;\n  }\n\n  &-toggle {\n    border-bottom: 1px solid rgba($border-color, 0.4);\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    position: relative;\n    cursor: pointer;\n\n    @include media('>xlarge') {\n      border: 0;\n      height: $large-header-height;\n    }\n\n    a {\n      width: calc(100% - 50px);\n      padding: $pad/2 $pad/2;\n      font-weight: 700;\n\n      @include media('>xlarge') {\n        width: auto;\n      }\n\n      @include media('>xxlarge') {\n        padding: $pad;\n      }\n    }\n\n    span {\n      display: none;\n      position: relative;\n      height: 100%;\n      width: rem(50);\n      padding: $pad/4 $pad/2;\n      text-align: right;\n      cursor: pointer;\n\n      svg {\n        width: rem(15);\n        height: rem(15);\n        right: 0;\n        top: rem(3);\n        position: relative;\n      }\n    }\n  }\n\n  &-item {\n    position: relative;\n    cursor: pointer;\n\n    &.active {\n      @include media('>xlarge') {\n        background-color: lighten($primary-color, 5%);\n      }\n    }\n\n    &.this-is-active {\n      background-color: darken($tan, 10%);\n\n      @include media('>xlarge') {\n        background-color: lighten($primary-color, 5%);\n      }\n\n      .c-primary-nav__list-toggle span svg {\n        transform: rotate(90deg);\n        right: rem(22);\n      }\n\n      .c-sub-nav__list {\n        display: block;\n      }\n    }\n\n    &.has-sub-nav {\n      .c-primary-nav__list-link {\n        @include media('>xlarge') {\n          transition: none;\n          font-size: rem(16);\n        }\n      }\n\n      .c-primary-nav__list-toggle {\n        position: relative;\n\n        span {\n          display: block;\n          height: rem(38);\n          width: rem(60);\n          padding: 0;\n          position: absolute;\n          right: 0;\n          top: 0;\n          border-left: 1px solid rgba($border-color, 0.4);\n          z-index: 999;\n\n          svg {\n            right: rem(21);\n            top: rem(9);\n          }\n\n          @include media('>xlarge') {\n            display: none;\n          }\n        }\n      }\n    }\n  }\n\n  &-link {\n    @include media('>xlarge') {\n      font-size: rem(12);\n      letter-spacing: rem(2);\n      white-space: nowrap;\n      color: $white;\n\n      &:hover {\n        color: $white;\n      }\n    }\n  }\n}\n\n.c-sub-nav__list {\n  background-color: $white;\n  display: none;\n\n  @include media('>xlarge') {\n    position: absolute;\n    left: 0;\n    width: rem(250);\n    box-shadow: 0 1px 2px rgba($gray, 0.5);\n  }\n\n  &-item {\n    &.active {\n      @include media('>xlarge') {\n        background-color: $tan;\n      }\n    }\n  }\n\n  &-link {\n    @include p;\n\n    padding: $pad/4 $pad;\n    display: block;\n    width: 100%;\n    border-bottom: 1px solid rgba($border-color, 0.4);\n\n    &:hover {\n      background-color: $tan;\n      color: $secondary-color;\n    }\n  }\n}\n\n.c-secondary-nav {\n  &__list {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    justify-content: center;\n  }\n\n  &__link {\n    padding: 0 $pad/2;\n    color: $secondary-color;\n\n    &.is-active {\n      color: $primary-color;\n    }\n  }\n}\n\n.c-breadcrumbs {\n  span {\n    color: $gray;\n  }\n}\n","/* ------------------------------------*\\\n    $PAGE SECTIONS\n\\*------------------------------------ */\n\n.c-section {\n  padding: $pad*2 0;\n\n  @include media('>medium') {\n    padding: $pad*4 0;\n  }\n}\n\n.c-slideshow {\n  &__image {\n    position: relative;\n    min-height: 70vh;\n    z-index: 0;\n  }\n\n  &__content {\n    z-index: 1;\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    margin: auto;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n}\n\n.c-section-hero {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: auto;\n\n  &::after {\n    z-index: 1 !important;\n  }\n\n  &--short {\n    min-height: rem(250);\n\n    @include media('>medium') {\n      min-height: rem(350);\n    }\n\n    @include media('>xlarge') {\n      min-height: rem(450);\n    }\n  }\n\n  &--tall {\n    min-height: rem(350);\n\n    @include media('>medium') {\n      min-height: 70vh;\n    }\n  }\n\n  &__content {\n    position: relative;\n    z-index: 2;\n  }\n\n  &__caption {\n    position: absolute;\n    z-index: 999;\n    bottom: rem(5);\n    left: rem(5);\n  }\n}\n\n.c-section-events {\n  &__title {\n    position: relative;\n    z-index: 1;\n\n    &::after {\n      content: \"Happenings\";\n      font-size: rem(144);\n      line-height: 1;\n      color: $white;\n      opacity: 0.1;\n      position: absolute;\n      z-index: 0;\n      top: rem(-72);\n      left: 0;\n      right: 0;\n      bottom: 0;\n      margin: auto;\n      display: none;\n\n      @include media('>medium') {\n        display: block;\n      }\n    }\n  }\n\n  &__feed {\n    z-index: 2;\n  }\n}\n\n.c-section-news {\n  &__title {\n    position: relative;\n    z-index: 1;\n    margin-top: $space*2;\n\n    @include media('>medium') {\n      margin-top: $space*4;\n    }\n\n    &::after {\n      content: \"Stay in the Loop\";\n      font-size: rem(144);\n      line-height: 1;\n      color: $white;\n      opacity: 0.1;\n      position: absolute;\n      z-index: 0;\n      top: rem(-72);\n      left: 0;\n      right: 0;\n      bottom: 0;\n      margin: auto;\n      display: none;\n\n      @include media('>medium') {\n        display: block;\n      }\n    }\n\n    + .c-section {\n      z-index: 2;\n      padding-top: $pad*2;\n    }\n  }\n}\n\n.c-section-related {\n  padding-bottom: $pad;\n}\n\n.c-section__featured-pages {\n  position: relative;\n\n  &::after {\n    content: \"\";\n    display: block;\n    width: 100%;\n    height: 100%;\n    z-index: -2;\n    background: $secondary-color;\n    position: absolute;\n    top: 0;\n    left: 0;\n  }\n}\n","/* ------------------------------------*\\\n    $SPECIFIC FORMS\n\\*------------------------------------ */\n\n/* Chrome/Opera/Safari */\n::-webkit-input-placeholder {\n  color: $gray;\n}\n\n/* Firefox 19+ */\n::-moz-placeholder {\n  color: $gray;\n}\n\n/* IE 10+ */\n:-ms-input-placeholder {\n  color: $gray;\n}\n\n/* Firefox 18- */\n:-moz-placeholder {\n  color: $gray;\n}\n\nlabel {\n  margin-top: $space;\n  width: 100%;\n\n  @include u-font--secondary--xs;\n}\n\nselect {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  cursor: pointer;\n  background: url('../../assets/images/o-arrow-down--small.svg') $white center right rem(10) no-repeat;\n  background-size: rem(10);\n}\n\ninput[type=email],\ninput[type=number],\ninput[type=search],\ninput[type=tel],\ninput[type=text],\ninput[type=url],\ntextarea,\nselect {\n  width: 100%;\n  font-size: rem(16);\n\n  &:focus {\n    border-color: $secondary-color;\n  }\n}\n\ninput[type=checkbox],\ninput[type=radio] {\n  outline: none;\n  border: none;\n  margin: 0 rem(5) 0 0;\n  height: rem(20);\n  width: rem(20);\n  line-height: rem(20);\n  background-size: rem(20);\n  background-repeat: no-repeat;\n  background-position: center center;\n  cursor: pointer;\n  display: block;\n  float: left;\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  -webkit-appearance: none;\n  background-color: $white;\n  position: relative;\n  top: rem(3);\n}\n\ninput[type=radio] {\n  border-radius: rem(50);\n}\n\ninput[type=checkbox],\ninput[type=radio] {\n  border-width: 1px;\n  border-style: solid;\n  border-color: $border-color;\n}\n\ninput[type=checkbox]:checked {\n  border-color: $secondary-color;\n  background: $secondary-color url('../assets/images/o-icon--check.svg') center center no-repeat;\n}\n\ninput[type=radio]:checked {\n  border-color: $secondary-color;\n  background: $secondary-color url('../assets/images/o-icon--radio.svg') center center no-repeat;\n}\n\ninput[type=checkbox] + span,\ninput[type=radio] + span {\n  display: inline-block;\n  cursor: pointer;\n  position: relative;\n}\n\ninput[type=submit] {\n  color: $white;\n  padding-right: $pad;\n  cursor: pointer;\n}\n\ndiv.wpcf7 {\n  margin: 0 auto;\n}\n\n.wpcf7-form-control.wpcf7-checkbox,\n.wpcf7-form-control.wpcf7-radio {\n  width: 100%;\n  padding: 0;\n  margin: 0;\n  display: flex;\n  flex-direction: column;\n  margin-top: $space;\n\n  .wpcf7-list-item {\n    margin-top: $space/4;\n    margin-left: 0;\n  }\n}\n\nlabel + .wpcf7-form-control-wrap {\n  .wpcf7-form-control {\n    margin-top: 0;\n  }\n}\n\n.o-filter-select {\n  padding: 0;\n  border: 0;\n  outline: 0;\n  color: $secondary-color;\n  width: rem(125);\n  margin-left: $space;\n\n  @include p;\n}\n","/* Slider */\n.slick-slider {\n  position: relative;\n  display: block;\n  box-sizing: border-box;\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  -ms-touch-action: pan-y;\n  touch-action: pan-y;\n  -webkit-tap-highlight-color: transparent;\n}\n\n.slick-list {\n  position: relative;\n  overflow: hidden;\n  display: block;\n  margin: 0;\n  padding: 0;\n\n  &:focus {\n    outline: none;\n  }\n\n  &.dragging {\n    cursor: pointer;\n    cursor: hand;\n  }\n}\n\n.slick-slider .slick-list,\n.slick-slider .slick-track {\n  -webkit-transform: translate3d(0, 0, 0);\n  -moz-transform: translate3d(0, 0, 0);\n  -ms-transform: translate3d(0, 0, 0);\n  -o-transform: translate3d(0, 0, 0);\n  transform: translate3d(0, 0, 0);\n}\n\n.slick-track {\n  position: relative;\n  left: 0;\n  top: 0;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n\n  &::after,\n  &::before {\n    content: \"\";\n    display: table;\n  }\n\n  &::after {\n    clear: both;\n  }\n\n  .slick-loading & {\n    visibility: hidden;\n  }\n}\n\n.slick-slide {\n  float: left;\n  height: 100%;\n  min-height: 1px;\n\n  [dir=\"rtl\"] & {\n    float: right;\n  }\n\n  img {\n    display: block;\n  }\n\n  &.slick-loading img {\n    display: none;\n  }\n\n  display: none;\n\n  &.dragging img {\n    pointer-events: none;\n  }\n\n  .slick-initialized & {\n    display: block;\n  }\n\n  .slick-loading & {\n    visibility: hidden;\n  }\n\n  .slick-vertical & {\n    display: block;\n    height: auto;\n    border: 1px solid transparent;\n  }\n}\n\n.slick-arrow.slick-hidden {\n  display: none;\n}\n\n.slick-slideshow {\n  .slick-slide {\n    visibility: hidden;\n    opacity: 0;\n    background-color: $black !important;\n    z-index: -1;\n    transition: all 0.5s cubic-bezier(0.28, 0, 0.18, 1) !important;\n\n    &.slick-active {\n      z-index: 1;\n      visibility: visible;\n      opacity: 1 !important;\n    }\n  }\n\n  &.slick-slider .slick-background {\n    transition: transform 11.5s cubic-bezier(0.28, 0, 0.18, 1);\n    transition-delay: 0.25s;\n    transform: scale(1.1, 1.1);\n  }\n\n  &.slick-slider .slick-active > .slick-background {\n    transform: scale(1.001, 1.001) translate3d(0, 0, 0);\n    transform-origin: 50% 50%;\n  }\n}\n\n.slick-arrow {\n  display: block;\n  width: rem(60);\n  height: rem(60);\n  background-color: $black;\n  position: absolute;\n  top: 50%;\n  z-index: 99;\n  cursor: pointer;\n  transform: translateY(-50%);\n  transition: all 0.25s ease;\n\n  &:hover {\n    background-color: $secondary-color;\n  }\n\n  @include media('<=small') {\n    display: none !important;\n  }\n}\n\n.slick-gallery .slick-dots {\n  height: rem(40);\n  line-height: rem(40);\n  width: 100%;\n  list-style: none;\n  text-align: center;\n\n  li {\n    position: relative;\n    display: inline-block;\n    margin: 0;\n    padding: 0 rem(5);\n    cursor: pointer;\n\n    button {\n      padding: 0;\n      border-radius: rem(50);\n      border: 0;\n      display: block;\n      height: rem(10);\n      width: rem(10);\n      outline: none;\n      line-height: 0;\n      font-size: 0;\n      color: transparent;\n      background: $gray;\n    }\n\n    &::before,\n    button::after {\n      display: none !important;\n    }\n\n    &.slick-active {\n      button {\n        background-color: $secondary-color;\n      }\n    }\n  }\n}\n\n////////////////////////\n//      Settings      //\n////////////////////////\n\n// overlay\n$mfp-overlay-color:                   #0b0b0b !default;                    // Color of overlay screen\n$mfp-overlay-opacity:                 0.8 !default;                        // Opacity of overlay screen\n$mfp-shadow:                          0 0 8px rgba(0, 0, 0, 0.6) !default; // Shadow on image or iframe\n\n// spacing\n$mfp-popup-padding-left:              8px !default;                        // Padding from left and from right side\n$mfp-popup-padding-left-mobile:       6px !default;                        // Same as above, but is applied when width of window is less than 800px\n\n$mfp-z-index-base:                    9999 !default;                       // Base z-index of popup\n\n// controls\n$mfp-include-arrows:                  true !default;                       // Include styles for nav arrows\n$mfp-controls-opacity:                0.65 !default;                       // Opacity of controls\n$mfp-controls-color:                  #fff !default;                       // Color of controls\n$mfp-controls-border-color:           #3f3f3f !default; \t                 // Border color of controls\n$mfp-inner-close-icon-color:          #333 !default;                       // Color of close button when inside\n$mfp-controls-text-color:             #ccc !default;                       // Color of preloader and \"1 of X\" indicator\n$mfp-controls-text-color-hover:       #fff !default;                       // Hover color of preloader and \"1 of X\" indicator\n\n// Iframe-type options\n$mfp-include-iframe-type:             true !default;                       // Enable Iframe-type popups\n$mfp-iframe-padding-top:              40px !default;                       // Iframe padding top\n$mfp-iframe-background:               #000 !default;                       // Background color of iframes\n$mfp-iframe-max-width:                900px !default;                      // Maximum width of iframes\n$mfp-iframe-ratio:                    9/16 !default;                       // Ratio of iframe (9/16 = widescreen, 3/4 = standard, etc.)\n\n// Image-type options\n$mfp-include-image-type:              true !default;                       // Enable Image-type popups\n$mfp-image-background:                #444 !default;\n$mfp-image-padding-top:               60px !default;                       // Image padding top\n$mfp-image-padding-bottom:            60px !default;                       // Image padding bottom\n$mfp-include-mobile-layout-for-image: true !default;                       // Removes paddings from top and bottom\n\n// Image caption options\n$mfp-caption-title-color:             #f3f3f3 !default;                    // Caption title color\n$mfp-caption-subtitle-color:          #bdbdbd !default;                    // Caption subtitle color\n\n// A11y\n$mfp-use-visuallyhidden:              false !default;                      // Hide content from browsers, but make it available for screen readers\n\n/* Magnific Popup CSS */\n\n////////////////////////\n//\n// Contents:\n//\n// 1. General styles\n//    - Transluscent overlay\n//    - Containers, wrappers\n//    - Cursors\n//    - Helper classes\n// 2. Appearance\n//    - Preloader & text that displays error messages\n//    - CSS reset for buttons\n//    - Close icon\n//    - \"1 of X\" counter\n//    - Navigation (left/right) arrows\n//    - Iframe content type styles\n//    - Image content type styles\n//    - Media query where size of arrows is reduced\n//    - IE7 support\n//\n////////////////////////\n\n////////////////////////\n// 1. General styles\n////////////////////////\n\n// Transluscent overlay\n.mfp-bg {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: $mfp-z-index-base + 2;\n  overflow: hidden;\n  position: fixed;\n  background: $mfp-overlay-color;\n  opacity: $mfp-overlay-opacity;\n}\n\n// Wrapper for popup\n.mfp-wrap {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  z-index: $mfp-z-index-base + 3;\n  position: fixed;\n  outline: none !important;\n  -webkit-backface-visibility: hidden; // fixes webkit bug that can cause \"false\" scrollbar\n}\n\n// Root container\n.mfp-container {\n  text-align: center;\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  left: 0;\n  top: 0;\n  padding: 0 $mfp-popup-padding-left;\n  box-sizing: border-box;\n}\n\n// Vertical centerer helper\n.mfp-container {\n  &::before {\n    content: '';\n    display: inline-block;\n    height: 100%;\n    vertical-align: middle;\n  }\n}\n\n// Remove vertical centering when popup has class `mfp-align-top`\n.mfp-align-top {\n  .mfp-container {\n    &::before {\n      display: none;\n    }\n  }\n}\n\n// Popup content holder\n.mfp-content {\n  position: relative;\n  display: inline-block;\n  vertical-align: middle;\n  margin: 0 auto;\n  text-align: left;\n  z-index: $mfp-z-index-base + 5;\n}\n\n.mfp-inline-holder,\n.mfp-ajax-holder {\n  .mfp-content {\n    width: 100%;\n    cursor: auto;\n  }\n}\n\n// Cursors\n.mfp-ajax-cur {\n  cursor: progress;\n}\n\n.mfp-zoom-out-cur {\n  overflow: hidden;\n\n  &,\n  .mfp-image-holder .mfp-close {\n    cursor: -moz-zoom-out;\n    cursor: -webkit-zoom-out;\n    cursor: zoom-out;\n  }\n}\n\n.mfp-zoom {\n  cursor: pointer;\n  cursor: -webkit-zoom-in;\n  cursor: -moz-zoom-in;\n  cursor: zoom-in;\n}\n\n.mfp-auto-cursor {\n  .mfp-content {\n    cursor: auto;\n  }\n}\n\n.mfp-close,\n.mfp-arrow,\n.mfp-preloader,\n.mfp-counter {\n  user-select: none;\n}\n\n// Hide the image during the loading\n.mfp-loading {\n  &.mfp-figure {\n    display: none;\n  }\n}\n\n// Helper class that hides stuff\n@if $mfp-use-visuallyhidden {\n  // From HTML5 Boilerplate https://github.com/h5bp/html5-boilerplate/blob/v4.2.0/doc/css.md#visuallyhidden\n  .mfp-hide {\n    border: 0 !important;\n    clip: rect(0 0 0 0) !important;\n    height: 1px !important;\n    margin: -1px !important;\n    overflow: hidden !important;\n    padding: 0 !important;\n    position: absolute !important;\n    width: 1px !important;\n  }\n}\n\n@else {\n  .mfp-hide {\n    display: none !important;\n  }\n}\n\n////////////////////////\n// 2. Appearance\n////////////////////////\n\n// Preloader and text that displays error messages\n.mfp-preloader {\n  color: $mfp-controls-text-color;\n  position: absolute;\n  top: 50%;\n  width: auto;\n  text-align: center;\n  margin-top: -0.8em;\n  left: 8px;\n  right: 8px;\n  z-index: $mfp-z-index-base + 4;\n\n  a {\n    color: $mfp-controls-text-color;\n\n    &:hover {\n      color: $mfp-controls-text-color-hover;\n    }\n  }\n}\n\n// Hide preloader when content successfully loaded\n.mfp-s-ready {\n  .mfp-preloader {\n    display: none;\n  }\n}\n\n// Hide content when it was not loaded\n.mfp-s-error {\n  .mfp-content {\n    display: none;\n  }\n}\n\n// CSS-reset for buttons\nbutton {\n  &.mfp-close,\n  &.mfp-arrow {\n    cursor: pointer;\n    border: 0;\n    -webkit-appearance: none;\n    display: block;\n    outline: none;\n    padding: 0;\n    z-index: $mfp-z-index-base + 6;\n    box-shadow: none;\n    touch-action: manipulation;\n  }\n\n  &::-moz-focus-inner {\n    padding: 0;\n    border: 0;\n  }\n\n  &::after,\n  &::before {\n    display: none;\n  }\n}\n\n// Close icon\n.mfp-close {\n  width: 100%;\n  min-width: rem(50);\n  height: rem(50);\n  position: fixed;\n  right: 0;\n  top: 0;\n  text-decoration: none;\n  text-align: center;\n  opacity: $mfp-controls-opacity;\n  padding: 0 0 $space 0;\n  background: transparent url('../assets/images/o-icon--close.svg') top right $space-half no-repeat;\n  background-size: rem(30);\n  text-indent: 9999px;\n  margin-top: $space-half;\n\n  &:hover,\n  &:focus {\n    opacity: 1;\n    background-color: transparent;\n  }\n\n  @include media('>medium') {\n    position: absolute;\n    height: rem(30);\n    margin-top: rem(15);\n  }\n}\n\n// \"1 of X\" counter\n.mfp-counter {\n  position: absolute;\n  top: 0;\n  right: 0;\n  color: $mfp-controls-text-color;\n  font-size: rem(14);\n  line-height: rem(18);\n  white-space: nowrap;\n  font-family: $font-primary;\n  font-weight: bold;\n}\n\n// Navigation arrows\n@if $mfp-include-arrows {\n  .mfp-arrow {\n    opacity: $mfp-controls-opacity;\n    padding: $pad-half;\n    width: rem(70);\n    height: 70%;\n    display: block;\n    position: absolute;\n    cursor: pointer;\n    top: 50%;\n    transform: translateY(-50%);\n\n    &:hover,\n    &:focus {\n      opacity: 1;\n      background-color: transparent;\n    }\n  }\n\n  .mfp-arrow-left {\n    left: 0;\n    background: transparent url('../assets/images/o-arrow-carousel--left.svg') center center no-repeat;\n    background-size: auto rem(50);\n  }\n\n  .mfp-arrow-right {\n    right: 0;\n    background: transparent url('../assets/images/o-arrow-carousel--right.svg') center center no-repeat;\n    background-size: auto rem(50);\n  }\n}\n\n// Image content type\n@if $mfp-include-image-type {\n  /* Main image in popup */\n  img {\n    &.mfp-img {\n      width: auto;\n      max-width: 100%;\n      height: auto;\n      display: block;\n      line-height: 0;\n      box-sizing: border-box;\n      padding: $mfp-image-padding-top 0 $mfp-image-padding-bottom;\n      margin: 0 auto;\n    }\n  }\n\n  /* The shadow behind the image */\n  .mfp-figure {\n    line-height: 0;\n\n    &::after {\n      content: '';\n      position: absolute;\n      left: 0;\n      top: $mfp-image-padding-top;\n      bottom: $mfp-image-padding-bottom;\n      display: block;\n      right: 0;\n      width: auto;\n      height: auto;\n      z-index: -1;\n      box-shadow: $mfp-shadow;\n      background: $mfp-image-background;\n    }\n\n    small {\n      color: $mfp-caption-subtitle-color;\n      display: block;\n      font-size: 12px;\n      line-height: 14px;\n    }\n\n    figure {\n      margin: 0;\n    }\n  }\n\n  .mfp-bottom-bar {\n    margin-top: -$mfp-image-padding-bottom + 4;\n    position: absolute;\n    top: 100%;\n    left: 0;\n    width: 100%;\n    cursor: auto;\n  }\n\n  .mfp-title {\n    text-align: left;\n    line-height: 18px;\n    color: $mfp-caption-title-color;\n    word-wrap: break-word;\n    padding-right: 36px; // leave some space for counter at right side\n  }\n\n  .mfp-image-holder {\n    .mfp-content {\n      max-width: 100%;\n    }\n  }\n\n  .mfp-gallery {\n    .mfp-image-holder {\n      .mfp-figure {\n        cursor: pointer;\n      }\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $ARTICLE\n\\*------------------------------------ */\n\n.c-article__content {\n  display: flex;\n  flex-direction: column-reverse;\n  flex-wrap: nowrap;\n\n  @include media('>small') {\n    flex-direction: row;\n\n    &--left {\n      width: rem(60);\n      flex: auto;\n      margin-right: $pad*2;\n    }\n\n    &--right {\n      width: calc(100% - 100px);\n    }\n  }\n}\n\n.c-article__share {\n  display: flex;\n  flex-direction: row;\n  justify-content: flex-start;\n  align-items: center;\n  text-align: center;\n  margin-top: $space*2;\n  z-index: 1;\n\n  @include media('>small') {\n    margin-top: 0;\n    flex-direction: column;\n    justify-content: center;\n  }\n\n  &-link {\n    margin-left: $space/2;\n\n    @include media('>small') {\n      margin-left: 0;\n      margin-top: $space/2;\n    }\n  }\n}\n\n.c-article__nav {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: nowrap;\n  justify-content: space-between;\n  border-top: 1px solid $gray;\n  padding-top: $pad;\n\n  &--inner {\n    width: 50%;\n\n    &:first-child {\n      padding-right: $pad/2;\n    }\n\n    &:last-child {\n      padding-left: $pad/2;\n    }\n  }\n}\n\n.c-article-product {\n  .c-article__body {\n    display: flex;\n    flex-direction: column;\n\n    @include media('>medium') {\n      flex-direction: row;\n    }\n\n    .c-article--left {\n      @include media('>medium') {\n        width: 40%;\n        padding-right: $pad;\n      }\n    }\n\n    .c-article--right {\n      @include media('>medium') {\n        width: 60%;\n        padding-left: $pad;\n      }\n    }\n  }\n\n  .c-article__footer {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n\n    @include media('>small') {\n      flex-direction: row;\n      justify-content: space-between;\n    }\n\n    &--left {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      justify-content: center;\n\n      > * {\n        margin: 0 $space 0 0;\n      }\n    }\n\n    &--right {\n      @include media('<=small') {\n        margin-top: $space;\n      }\n\n      .c-article__share {\n        margin: 0;\n        flex-direction: row;\n        align-items: center;\n        justify-content: center;\n\n        > * {\n          margin-top: 0;\n\n          @include media('>small') {\n            margin-left: $space/2;\n          }\n        }\n      }\n    }\n  }\n}\n\n// Article Body list styles from u-font--styles.scss\nol,\nul {\n  .c-article__body & {\n    margin-left: 0;\n    margin-top: 0;\n\n    li {\n      list-style: none;\n      padding-left: $pad;\n      text-indent: rem(-10);\n\n      &::before {\n        color: $primary-color;\n        width: rem(10);\n        display: inline-block;\n        font-size: rem(30);\n      }\n\n      li {\n        list-style: none;\n      }\n    }\n  }\n}\n\nol {\n  .c-article__body & {\n    counter-reset: item;\n\n    li {\n      &::before {\n        content: counter(item) \". \";\n        counter-increment: item;\n        font-size: 90%;\n      }\n\n      li {\n        counter-reset: item;\n\n        &::before {\n          content: \"\\002010\";\n        }\n      }\n    }\n  }\n}\n\nul {\n  .c-article__body & {\n    li {\n      &::before {\n        content: \"\\002022\";\n      }\n\n      li {\n        &::before {\n          content: \"\\0025E6\";\n        }\n      }\n    }\n  }\n}\n\n.c-article {\n  margin-left: auto;\n  margin-right: auto;\n  padding-top: $pad*2;\n  padding-bottom: $pad*4;\n}\n\n.c-article__body {\n  &__image {\n    outline: 0;\n  }\n\n  > *,\n  figcaption,\n  ul {\n    max-width: rem(700);\n    margin: 0 auto;\n  }\n\n  > p:first-child::first-letter {\n    color: $secondary-color;\n    float: left;\n    font-size: rem(60);\n    margin-top: rem(15);\n    margin-right: rem(10);\n  }\n\n  a {\n    text-decoration: underline;\n  }\n\n  .o-button {\n    text-decoration: none;\n  }\n\n  p,\n  ul,\n  ol,\n  dt,\n  dd {\n    @include p;\n  }\n\n  p span,\n  p strong span {\n    font-family: $font !important;\n  }\n\n  strong {\n    font-weight: bold;\n  }\n\n  > p:empty,\n  > h2:empty,\n  > h3:empty {\n    display: none;\n  }\n\n  > h1,\n  > h2,\n  > h3,\n  > h4,\n  > h5 {\n    margin-top: $space*2;\n\n    &:first-child {\n      margin-top: 0;\n    }\n  }\n\n  > h1 {\n    @include u-font--primary--l;\n  }\n\n  > h2 {\n    @include u-font--primary--m;\n  }\n\n  > h3 {\n    @include u-font--xl;\n  }\n\n  h4,\n  h5 {\n    @include u-font--secondary--s;\n\n    color: $secondary-color;\n    margin-bottom: rem(-30);\n  }\n\n  h1 + ul,\n  h2 + ul,\n  h3 + ul,\n  h4 + ul,\n  h5 + ul {\n    display: block;\n    margin-top: rem(30);\n  }\n\n  img {\n    height: auto;\n  }\n\n  hr {\n    margin-top: rem(15);\n    margin-bottom: rem(15);\n\n    @include media('>large') {\n      margin-top: rem(30);\n      margin-bottom: rem(30);\n    }\n  }\n\n  figcaption {\n    @include u-font--s;\n  }\n\n  figure {\n    max-width: none;\n    width: auto !important;\n\n    img {\n      margin: 0 auto;\n      display: block;\n    }\n  }\n\n  blockquote {\n    p {\n      @include u-font--xl;\n\n      color: $secondary-color;\n      font-style: italic;\n    }\n\n    padding-left: $pad;\n    border-left: 1px solid $gray;\n\n    @include media('>large') {\n      padding-left: $pad*2;\n    }\n  }\n\n  .wp-caption-text {\n    display: block;\n    line-height: 1.3;\n    text-align: left;\n    margin-top: rem(5);\n  }\n\n  .aligncenter {\n    margin-left: auto;\n    margin-right: auto;\n    text-align: center;\n\n    figcaption {\n      text-align: center;\n    }\n  }\n\n  .alignleft,\n  .alignright {\n    min-width: 50%;\n    max-width: 50%;\n\n    img {\n      width: 100%;\n    }\n  }\n\n  .alignleft {\n    float: left;\n    margin: 0 $space-and-half $space 0;\n  }\n\n  .alignright {\n    float: right;\n    margin: 0 0 $space $space-and-half;\n\n    @include media('>large') {\n      margin-right: rem(-100);\n    }\n  }\n\n  .size-full {\n    width: auto;\n  }\n\n  .size-thumbnail {\n    max-width: rem(400);\n    height: auto;\n  }\n}\n\n.c-article--right {\n  .alignleft,\n  .alignright {\n    min-width: 33.33%;\n    max-width: 33.33%;\n\n    img {\n      width: 100%;\n    }\n  }\n\n  .alignright {\n    @include media('>large') {\n      margin-right: 0;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $SIDEBAR\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $FOOTER\n\\*------------------------------------ */\n\n.c-footer {\n  padding-top: $pad*2;\n  padding-bottom: $pad*2;\n}\n\n.c-footer--inner {\n  position: relative;\n  overflow: hidden;\n}\n\n.c-footer__links {\n  display: flex;\n  flex-direction: column;\n  width: calc(100% - 40px);\n\n  @include media('>medium') {\n    width: 100%;\n    flex-direction: row;\n    justify-content: space-between;\n    flex-basis: rem(300);\n\n    > div {\n      width: 40%;\n      max-width: rem(400);\n    }\n  }\n}\n\n.c-footer__nav {\n  @include media('>medium') {\n    margin-top: 0 !important;\n  }\n\n  &-list {\n    column-count: 2;\n    column-gap: $space*2;\n    column-width: rem(140);\n\n    a {\n      @include u-font--secondary--xs;\n\n      color: $white;\n      padding-bottom: $pad/2;\n      letter-spacing: rem(2.5);\n      display: block;\n\n      &:hover {\n        color: $gray;\n      }\n    }\n  }\n}\n\n.c-footer__scroll {\n  width: rem(200);\n  height: rem(60);\n  display: block;\n  transform: rotate(-90deg);\n  position: absolute;\n  left: auto;\n  right: rem(-110);\n  top: rem(-10);\n  z-index: 4;\n\n  @include media('>medium') {\n    top: rem(20);\n    left: rem(-70);\n    bottom: auto;\n    margin: 0 auto !important;\n  }\n\n  a {\n    width: 100%;\n    height: auto;\n    display: block;\n  }\n}\n\n.c-footer__social {\n  position: relative;\n\n  &::before,\n  &::after {\n    content: \"\";\n    display: block;\n    height: rem(1);\n    background-color: $gray;\n    top: 0;\n    bottom: 0;\n    margin: auto 0;\n    width: calc(50% - 40px);\n    position: absolute;\n  }\n\n  &::before {\n    left: 0;\n  }\n\n  &::after {\n    right: 0;\n  }\n\n  a {\n    display: block;\n    width: rem(40);\n    height: rem(40);\n    border: 1px solid $gray;\n    margin: 0 auto;\n    text-align: center;\n\n    .u-icon {\n      position: relative;\n      top: rem(8);\n\n      svg {\n        width: rem(20);\n        height: rem(20);\n        margin: 0 auto;\n      }\n    }\n\n    &:hover {\n      background-color: $gray;\n    }\n  }\n}\n\n.c-footer__copyright {\n  display: flex;\n  flex-direction: column;\n  margin-top: $space !important;\n\n  @include media('>large') {\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n  }\n\n  @include media('<=large') {\n    > div {\n      margin-top: $space/2;\n\n      &:first-child {\n        margin-top: 0;\n      }\n    }\n  }\n}\n\n.c-footer__affiliate {\n  width: rem(140);\n}\n","/* ------------------------------------*\\\n    $HEADER\n\\*------------------------------------ */\n\n.c-utility {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  height: rem(40);\n}\n\n.c-utility__search {\n  form {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    input,\n    button {\n      height: rem(40);\n      margin: 0;\n      border: 0;\n      padding: 0;\n    }\n\n    input {\n      width: 100%;\n      text-align: right;\n      max-width: rem(120);\n\n      @include media('>small') {\n        max-width: none;\n        min-width: rem(250);\n      }\n    }\n\n    input::placeholder {\n      @include u-font--secondary--xs;\n\n      color: $gray;\n      text-align: right;\n    }\n\n    button {\n      padding-right: 0;\n      padding-left: $pad;\n    }\n  }\n}\n\n.c-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  position: relative;\n  height: $small-header-height;\n\n  @include media('>xlarge') {\n    height: $large-header-height;\n  }\n}\n\n.c-logo {\n  display: block;\n  height: auto;\n  width: rem(190);\n  position: relative;\n  left: rem(-10);\n\n  @include media('>xlarge') {\n    height: auto;\n    width: rem(250);\n    left: 0;\n  }\n}\n\n.c-page-header {\n  position: relative;\n  z-index: 1;\n  padding-top: $pad*2;\n\n  &__icon {\n    background: $white;\n    border-radius: 100%;\n    width: rem(150);\n    height: rem(150);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin: rem(-100) auto 0 auto;\n  }\n\n  + .c-section-events {\n    margin-top: $space*4;\n  }\n}\n","/* ------------------------------------*\\\n    $MAIN CONTENT AREA\n\\*------------------------------------ */\n\n.c-article .yarpp-related {\n  display: none;\n}\n\n.yarpp-related {\n  padding: 0;\n  margin: 0;\n  font-weight: normal;\n\n  h3 {\n    font-weight: normal;\n  }\n}\n\n.page.business-partners {\n  img {\n    width: calc(50% - 45px);\n    height: auto;\n    margin: $space;\n    display: inline-block;\n  }\n}\n\n.page.events {\n  .c-block,\n  .c-block__date {\n    background-color: $tan;\n  }\n}\n","/* ------------------------------------*\\\n    $ANIMATIONS & TRANSITIONS\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $BORDERS\n\\*------------------------------------ */\n\n.u-border {\n  border: 1px solid $border-color;\n}\n\n.u-border--white {\n  background-color: $white;\n  border-color: $white;\n}\n\n.u-border--black {\n  background-color: $black;\n  border-color: $black;\n}\n\n.u-hr--small {\n  width: rem(60);\n  height: rem(1);\n  background-color: $black;\n  border: 0;\n  outline: 0;\n  display: block;\n  margin: 0 auto;\n}\n\n.u-hr--white {\n  background-color: $white;\n}\n\n.u-hr--gray {\n  background-color: $gray;\n}\n\n.o-divider {\n  padding-left: $pad/2;\n  padding-right: $pad/2;\n  font-style: normal;\n}\n","/* ------------------------------------*\\\n    $COLOR MODIFIERS\n\\*------------------------------------ */\n\n/**\n * Text Colors\n */\n.u-color--black {\n  color: $black;\n}\n\n.u-color--white {\n  color: $white;\n  -webkit-font-smoothing: antialiased;\n}\n\n.u-color--gray {\n  color: $gray;\n}\n\n.u-color--primary {\n  color: $primary-color;\n}\n\n.u-color--secondary {\n  color: $secondary-color;\n}\n\n.u-color--tan {\n  color: $tan;\n}\n\n/**\n * Background Colors\n */\n.u-background-color--none {\n  background: none;\n}\n\n.u-background-color--white {\n  background-color: $white;\n}\n\n.u-background-color--black {\n  background-color: $black;\n}\n\n.u-background-color--primary {\n  background-color: $primary-color;\n}\n\n.u-background-color--secondary {\n  background-color: $secondary-color;\n}\n\n.u-background-color--tertiary {\n  background-color: $tertiary-color;\n}\n\n.u-background-color--tan {\n  background-